body {
  background-color: #fff;
  padding-top: 3rem;
  font-family: 'Inconsolata', monospace;
  width: 80vw;
  max-width: 500px;
  margin: auto; }

svg {
  display: block;
  background-color: #fff;
  width: 100%;
  margin: auto; }
  svg text {
    font-size: 1rem;
    fill: #222;
    text-anchor: middle;
    alignment-baseline: middle; }
  svg circle, svg line, svg rect, svg path {
    stroke-width: 1.5;
    stroke-linecap: butt; }

.tape {
  fill: none;
  stroke: #16a085; }

.reel {
  fill: none;
  stroke: #222; }

.tape-transport {
  fill: #fff;
  stroke: #222; }

#progressBar {
  fill: #16a085;
  stroke: none; }

.controls {
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: #fff;
  padding: 1rem 0 1.5rem 0; }
  .controls button {
    width: 2.5rem;
    height: 2.3rem;
    margin: 0 0.6rem;
    padding-top: 0.25rem;
    border: none;
    background-color: #95a5a6;
    border-bottom: 0.3rem solid #7f8c8d;
    transition: border-bottom-width 0.1s;
    border-radius: 0.15rem; }
    .controls button img {
      width: 80%; }
  .controls button:active {
    border-bottom-width: 0.15rem; }
  .controls button:focus {
    outline: 0; }

button#play {
  background-color: #e74c3c;
  border-bottom-color: #c0392b; }

.tape-player {
  background-color: #fff;
  display: block;
  margin: auto;
  width: 100%; }

.tape-player__counter {
  alignment-baseline: middle;
  fill: #222;
  font-size: 1rem;
  text-anchor: middle; }

.tape-player__progress--bar {
  fill: #ddd;
  stroke: none; }

.tape-player__progress--fill {
  stroke: none; }

.tape-player__tape {
  fill: none;
  stroke-width: 1.5; }

.tape-player__reel {
  fill: none;
  stroke: #222;
  stroke-width: 1.5; }

.tape-player__transport {
  fill: #fff;
  stroke: #222;
  stroke-width: 1.5; }

.tape-player__controls {
  background-color: #fff;
  display: flex;
  justify-content: center;
  padding: 1rem 0;
  width: 100%; }

.tape-player__button {
  background-color: #95a5a6;
  border: none;
  border-bottom: 0.25rem solid #7f8c8d;
  border-radius: 0.15rem;
  cursor: pointer;
  height: 2.3rem;
  margin: 0 0.6rem;
  padding: 0.35rem 0;
  width: 2.5rem;
  transition: border-bottom-width 0.1s; }
  .tape-player__button img {
    width: 1.3rem; }

.tape-player__button:active {
  border-bottom-width: 0.1rem; }

.tape-player__button:focus {
  outline: 0; }

.tape-player__button--red {
  background-color: #e74c3c;
  border-bottom-color: #c0392b; }

.tape-player__song-list {
  list-style: none;
  margin: auto;
  max-width: 400px;
  padding: 0;
  width: 100%; }

.tape-player__song {
  cursor: pointer;
  padding: 0.2rem;
  margin: 1rem;
  border-bottom: 0.175rem dashed;
  user-select: none; }
