@import 'normalize';

body {
  background-color: #fff;
  padding-top: 3rem;
  font-family: 'Inconsolata', monospace;
  width: 80vw;
  max-width: 500px;
  margin: auto;
}

svg {
  display: block;
  background-color: #fff;
  width: 100%;
  margin: auto;

  text {
    font-size: 1rem;
    fill: #222;
    text-anchor: middle;
    alignment-baseline: middle;
  }

  circle, line, rect, path {
    stroke-width: 1.5;
    stroke-linecap: butt;
  }
}

.tape {
  fill: none;
  stroke: #16a085;
}

.reel {
  fill: none;
  stroke: #222;
}

.tape-transport {
  fill: #fff;
  stroke: #222;
}

#progressBar {
  fill: #16a085;
  stroke: none;
}

.controls {
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: #fff;
  padding: 1rem 0 1.5rem 0;

  button {
    // max-width: 100px;
    width: 2.5rem;
    height: 2.3rem;
    margin: 0 0.6rem;
    padding-top: 0.25rem;
    border: none;
    background-color: #95a5a6;
    border-bottom: 0.3rem solid #7f8c8d;
    transition: border-bottom-width 0.1s;
    border-radius: 0.15rem;

    img {
      width: 80%;
    }
  }

  button:active { border-bottom-width: 0.15rem; }
  button:focus { outline: 0; }
}

button#play {
  background-color: #e74c3c;
  border-bottom-color: #c0392b;
}

