.tape-player {
  background-color: #fff;
  display: block;
  margin: auto;
  width: 100%;
}

.tape-player__counter {
  alignment-baseline: middle;
  fill: #222;
  font-size: 1rem;
  text-anchor: middle;
}

.tape-player__progress--bar {
  fill: #ddd;
  stroke: none;
}

.tape-player__progress--fill {
  stroke: none;
}

.tape-player__tape {
  fill: none;
  stroke-width: 1.5;
}

.tape-player__reel {
  fill: none;
  stroke: #222;
  stroke-width: 1.5;
}

.tape-player__transport {
  fill: #fff;
  stroke: #222;
  stroke-width: 1.5;
}

.tape-player__controls {
  background-color: #fff;
  display: flex;
  justify-content: center;
  padding: 1rem 0;
  width: 100%;
}

.tape-player__button {
  background-color: #95a5a6;
  border: none;
  border-bottom: 0.25rem solid #7f8c8d;
  border-radius: 0.15rem;
  cursor: pointer;
  height: 2.3rem;
  margin: 0 0.6rem;
  padding: 0.35rem 0;
  width: 2.5rem;

  transition: border-bottom-width 0.1s;

  img {
    width: 1.3rem;
  }
}

.tape-player__button:active { border-bottom-width: 0.1rem; }
.tape-player__button:focus { outline: 0; }

.tape-player__button--red {
  background-color: #e74c3c;
  border-bottom-color: #c0392b;
}

.tape-player__song-list {
  list-style: none;
  margin: auto;
  max-width: 400px;
  padding: 0;
  width: 100%;
}

.tape-player__song {
  cursor: pointer;
  padding: 0.2rem;
  margin: 1rem;
  border-bottom: 0.175rem dashed;
  user-select: none;
}
